@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
  text-decoration: none;
  color: black;
}

* {
  box-sizing: border-box;
}

.header{display:flex;flex-direction:column}.header .header__logo{align-self:center}.header .header__logo .header__src:hover{width:96px}.header .header__content{align-self:center}@media screen and (max-width: 600px){.header .header__content .header__content__text{font-size:1.6rem}}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.spinner__loading {
  border: 8px solid rgba(0, 0, 0, 0.1);
  border-left-color: #f15b23;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  z-index: 100;
  -webkit-animation: spin 1.2s linear infinite;
          animation: spin 1.2s linear infinite;
}

body {
  font-family: 'Bebas Neue', cursive;
  padding: 20px 60px;
}

